.recent-news{
    padding: 80px 0px;
    background-color: var (--color-light);

    &__list{
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: repeat(2, 255px);
        gap: var(--grid-gap);
        margin-bottom: 80px;
    }

    &__title{
        margin-bottom: 60px;
    }

    &__item{
        grid-column: 5 span;
        &:first-child{
            grid-column: 7 span;
            grid-row: 2 span;
        }
    }
}

.recent-article{
    overflow: hidden;
    border-radius: 4px;
    height: 100%;
    box-shadow: 0px 4px 4px -4px rgba(30, 33, 44, 0.03), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 80px 80px -20px rgba(154, 156, 165, 0.08);
    background-color: var(--color-light);
    &--small{
        .recent-article__descr{
            display: none;
        }

        .recent-article__text{
            padding: 12px 24px;
        }

        .recent-article__title{
            margin-bottom: 9px;
        }
    }

    &__text{
        padding: 20px 24px;
    }

    &__title{
        margin: 0;
        margin-bottom: 13px;
    }

    &__meta{
        margin-bottom: 17px;
    }

    &__descr{
        margin: 0;
        color: var(--color-gray800);
        font-size: 16px;
        line-height: 160%; /* 25.6px */
    }

    &__link{
        color: var(--color-gray900);
        font-size: 20px;
        font-weight: 700;
        line-height: 150%; /* 30px */
    }
}

.article-meta{
    color: var(--color-gray700);
    font-size: 14px;
    line-height: 150%; /* 21px */

    &__item{
        position: relative;
        color:inherit;

        &:not(:last-child)
        {
            margin-right: 20px;

            &::after {
                content: "";
                position: absolute;
                right: -13px;
                top: 2px;
                width: 1px;
                height: 11px;
                color: inherit;
                background-color: currentColor;
            }
        }

        &--comments{
            padding-left: 24px;
            background-image: url("../img/comments.svg");
            background-position: left center;
            background-size: 16px 16px;
            background-repeat: no-repeat;
        }
    }
}