html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1260px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-title{
  margin: 0;
  margin-bottom: 25px;
  color: var(--color-gray900);
  font-size: 72px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 1px;
  text-transform: uppercase;

  span{
    color: var(--color-primary);
  }

  &--home{
    color: var(--color-light);
  }
}

.main-descr{
  margin: 0;
  color: var(--color-gray700);
  font-size: 20px;
  line-height: 150%;
  

  &--home{
    margin-bottom: 61px;
    color: var(--color-light);
  }
}

.btn{
  border-radius: 4px;
  color: var(--color-light);
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  padding: 15px 38px;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;

  &--fill{
    background-color: var(--color-primary);
  }

  &--stroke{
    
    &-light{
      border-color: var(--color-light);
    }
  }
}

.title{
  margin: 0;
  margin-bottom: 25px;
  color: var (--color-gray900);
  font-size: 46px;
  font-weight: 700;
  line-height: 130%; /* 59.8px */

  &--center{
    text-align: center;
  }
}

.descr{
  margin: 0;
  margin-bottom: 60px;
  color: var(--color-gray700);
  font-size: 18px;
  line-height: 150%; /* 27px */

  &--center{
    text-align: center;
  }
}

.section-offset{
  padding-top: 120px;
}