.hero{
    padding-top: 220px;
    padding-bottom: 353px;

    background-image: url("../img/hero-bg.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: var(--color-gray);

    &__content{
        max-width: 595px;
    }

    &__btns{
        .btn{
            &:not(:last-child){
                margin-right: 20px;
            }
        }
    }
}